$button-color: #ddd;
$text-color: #807a0a;

#display {
  grid-area: display;
  text-align: center;
  margin: 1rem 1rem;
  border: .2rem solid $text-color;
  border-radius: .2rem;
  padding: .4rem .4rem;
  min-height: 30vh;
  background-color: darken(ivory, 1.5%);
  color: darken($text-color, 3%);
  overflow-y: scroll;
  height: 50vh;
  max-height: 50vh;
}

#display-headers {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#display-headers p {
  margin: 2px 10px;
}

#display-results {
  text-align: left;
}
